import React from 'react';

import { useQuery } from '@apollo/react-hooks';
import gql from "graphql-tag";

const myQuery = gql`
{
    swp_article {
          title 
    }
}
`;

function App() {
  const { data } = useQuery(myQuery);
  
  return (
    <div className="App">
      <header className="App-header">
        <p>
         Hello, I hold { data ? data.swp_article.length : 'No' } Objects
        </p>
      </header>
    </div>
  );
}

export default App;